import React, { useEffect, useState } from 'react'
import fire from '../../utils/Firebase'

import '../../css/pages/Signin.css'

import {useSelector, useDispatch} from 'react-redux'
import {userSliceActions} from '../../store/features/userSlice'


export default function Signin() {

    const dispatch = useDispatch()
    // const 

    const [email, setEmail] = useState("")
    const [password, setPassword] = useState("")

    const handleEmailInput = (e)=>{

        setEmail(e.target.value)

    }

    const handlePasswordChange = (e)=>{
        setPassword(e.target.value);
    }

    const handleLogin = (e)=>{

        e.preventDefault()

        fire
            .auth()
            .signInWithEmailAndPassword(email, password)
            .catch(err => {
                console.log("an error occured", err);
            })

    }

    useEffect(() => {
        
        fire
        .auth()
        .onAuthStateChanged((user)=>{

            if(user){
                dispatch(userSliceActions.logIn())
            }else{
                dispatch(userSliceActions.logOut())
            }

        })

    }, [])

    return (
        <div className="body">

            <section className="section--signin">

                <form action="" className="form">

                    <div className="form_header">
                        <h1 className="form_header h1-md">Login</h1>
                        <p>Access Your Account</p>
                    </div>

                    <div className="form_fields">

                        <label htmlFor="name" className="fm_label">
                            Email
                            <input 
                                value={email}
                                type="email" 
                                name="" 
                                id="" 
                                className="form_tb"
                                onChange= {handleEmailInput}
                            />
                        </label>

                        <label htmlFor="name" className="fm_label">
                            Password
                            <input 
                                value={password}
                                type="password" 
                                name="" id="" 
                                className="form_tb"
                                onChange = {handlePasswordChange}
                            />
                        </label>
                        
                        <div className="radio_label" >
                            <input type="checkbox" name="" id=""/> Login as admin
                        </div>

                        {/* <label htmlFor="">
                            <span>
                                <input type="checkbox" name="" id=""/> Login as admin
                            </span>
                            
                        </label> */}


                    </div>

                    <button 
                        className="btn btn--md btn--complementary btn--blk"
                        onClick={handleLogin}
                        >Sign In
                    </button>

                    </form>

                

                <div className="dimmer"></div>

            </section>
            
        </div>
    )
}
