import React, { useState } from 'react'
import {Link} from 'react-router-dom'

import '../../css/pages/Signup.css'
import fire from '../../utils/Firebase'

import {useDispatch} from 'react-redux'
import {userSliceActions} from '../../store/features/userSlice'


export default function Signup() {

    const dispatch = useDispatch()

    const [userProps, setUserProps] = useState({
        name: "",
        dob: "",
        address: "",
        email: "",
        phone: "",
        password: ""
    })

    const handleNameChange = (e)=>{

        const user = userProps
        user.name = e.target.value

        setUserProps(user)

    }

    const handleEmailChange = (e)=>{
        const user = userProps
        user.email = e.target.value

        setUserProps(user)
    }

    const handleDOBChange = (e)=>{

        const user = userProps
        user.dob = e.target.value

        setUserProps(user)

    }

    const handleAddressChange = (e)=>{

        const user = userProps
        user.address = e.target.value

        setUserProps(user)

    }

    const handlePhoneChange = (e)=>{
        const user = userProps
        user.phone = e.target.value

        setUserProps(user)
    }

    const handlePasswordChange = (e)=>{
        const user = userProps
        user.password = e.target.value

        setUserProps(user)
    }

    const handleSignup = (e)=>{
        e.preventDefault()
        
        fire.auth()
        .createUserWithEmailAndPassword(userProps.email, userProps.password)
        .then(()=>{
            dispatch(userSliceActions.logIn())
        })
        .catch((e)=>{
            console.log("an error occurred", e);
        })
    }

    return (
        <div className="body">

            <section className="section--signup">

                <form action="" className="form">

                    <div className="form_header">
                        <h1 className="form_header h1-md">Sign Up</h1>
                        <p>Register to begin your loan process</p>
                    </div>

                    <div className="form_fields">

                        <label htmlFor="name" className="fm_label">
                            Name (Surname, First, Others)
                            <input 
                                type="text" 
                                name="" 
                                id="" 
                                className="form_tb"
                                onChange= {handleNameChange}
                            />
                        </label>

                        <div className="radio_label" >
                            Marital Status
                            <label htmlFor="">
                                <span>
                                    <input value="married" type="radio" name="maritalstatus" id=""/>Married
                                </span>
                                <span>
                                    <input 
                                        value="single" 
                                        checked 
                                        type="radio" 
                                        name="maritalstatus" 
                                        id=""
                                    />
                                    Single
                                </span>
                            </label>
                        </div>

                        <label htmlFor="name" className="fm_label">
                            Date Of Birth
                            <input 
                                type="date" 
                                name="" 
                                id="" 
                                className="form_tb"
                                onChange = {handleDOBChange}
                            />
                        </label>

                        <label htmlFor="name" className="fm_label">
                            Address
                            <input 
                                type="address" 
                                name="" 
                                id="" 
                                className="form_tb"
                                onChange = {handleAddressChange}
                            />
                        </label>

                        <label htmlFor="name" className="fm_label">
                            Email
                            <input 
                                type="email" 
                                name="" 
                                id="" 
                                className="form_tb"
                                onChange = {handleEmailChange}
                            />
                        </label>

                        <label htmlFor="name" className="fm_label">
                            Phone
                            <input 
                                type="tel" 
                                name="" 
                                id="" 
                                className="form_tb"
                                onChange = {handlePhoneChange}
                            />
                        </label>

                        <label htmlFor="name" className="fm_label">
                            Password
                            <input
                                type="password" 
                                name="" 
                                id="" 
                                className="form_tb"
                                onChange = {handlePasswordChange}
                            />
                        </label>


                    </div>

                    <button 
                        className="btn btn--md btn--complementary btn--blk"
                        onClick = {handleSignup}
                        >
                            Proceed
                    </button>

                    </form>

                

                <div className="dimmer"></div>

            </section>
            
        </div>
    )
}
